 @import '../global.scss';
 
 .intro{
    background-color: white;
    display: flex;

    @include mobile{
        flex-direction: column;
        align-items: center;
    }
    .left{
        flex: 0.5;
        overflow: hidden;

        .imgContainer{
            width: 750px;
            height: 700px;
           background-color: crimson;
           border-radius: 50%;
           display: flex;
           align-items: flex-end;
           justify-content: center;
           float: right;

           @include mobile{
            align-items: flex-start;
           }

            img{
                height: 90%;

                @include mobile{
                    height: 50%;
                }
            }
        }
    }
    .right{
        flex: 0.5;
        position: relative;

        .wrapper{
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            padding-left: 50px;

            @include mobile{
                padding: 0;
                align-items: center;
            }
           

            h1{
                font-size: 60px;
                margin: 10px 0;

                @include mobile{
                    font-size: 40px;
                }
            }
            h2{
                font-size: 30px;
                @include mobile{
                    font-size: 28px;
                }
            }
            h3{
                font-size: 35px;
                @include mobile{
                    font-size: 30px;
                }
            }

                span{
                    font-size: inherit;
                    color: crimson;
                }
        }
            a{
                position: absolute;
                bottom: 10px;
                left: 40%;
                

                img{
                    width: 40px;
                    animation: arrowBlink 2s infinite;
                }
                @keyframes arrowBlink {
                    100%{
                        opacity: 0;
                    }
                }
            }
    }
}