@import "../../global.scss";

.topbar{
    width: 100%;
    height: 70px;
    background-color: white;
    color: $mainColor;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;

.wrapper{
    display: flex;
    align-items: center;
    padding: 10px 30px;
    justify-content: space-between;

    .left{
        display: flex;
        align-items: center;

        .logo{
            font-size: 40px;
            text-decoration: none;
            font-weight: 600;
            color: inherit;
            margin-right: 40px;
        }

        .itemContainer{
            display: flex;
            align-items: center;
            margin-left: 30px;

            @include mobile{
                display: none;
            }
        }
        .icon{
            font-size: 18px;
            margin-right: 5px;
        }
        span{
            font-size: 15px;
            font-weight: 500;
        }
    }
    
    .right{

        .hamburger{
            width: 32px;
            height: 23px;
            
            display: flex;
           flex-direction: column;
           justify-content: space-between;
           cursor: pointer;

           span{
               width: 100%;
               height: 3px;
               background-color: $mainColor;
               transform-origin: left;
               transition: all 2s ease;
            }
        }
    }
}

&.active{
    background-color: $mainColor;
    color: white;

    .hamburger{
        overflow-y: hidden;
        span{
            &:first-child{
                background-color: white;
                transform: rotate(40deg);
            }
            &:nth-child(2){
                opacity: 0;
            }
            &:last-child{
                background-color: white;
                transform: rotate(-40deg);
            }
        }
    }
}

}